.calc {
	background-color: #181818;
	color: $white;
}

.calc__cont {
	padding-bottom: 10px;
	padding-top: 120px;
	position: relative;
}



.calc__title {
	@extend .about__title;
	margin: 0;
	margin-bottom: 20px;
}

.calc__img {
	display: block;
	max-width: 100%;
	margin: 24px auto 30px;
	height: auto;
}

.calc__radio {
	margin-top: 7px;
	margin-bottom: 7px;
	input[type="radio"] {
		display: none;
	}

	label {
		font-size: px-to-rem(17px);
		line-height: px-to-rem(24px);
		position: relative;
		padding-left: 25px;
		cursor: pointer;
		display: inline-block;
		&::before {
			content: '';
			width: 16px;
			height: 16px;
			border: 1px solid $yellows;
			box-sizing: border-box;
			background: transparent;
			border-radius: 5px;
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			transition: 250ms ease-in-out;
		}
	}
	input[type="radio"]:checked + label::before {
		background-color: $yellows;
		transform: rotate(45deg);
		top: 15%;

	}
}

.calc__gr {
	padding: 0;
	border: none;
	margin: 20px 0;
}

.calc__leg {
	font-size: px-to-rem(14px);
	line-height: px-to-rem(17px);
	font-weight: 700;
	margin-bottom: 12px;
}


.calc__range {
	display: none;
	label {
		@include visually-hidden;
	}
}

.calc__square {
	background-color: transparent;
	display: block;
	width: 100%;
	border-radius: 5px;
	border: 1px solid rgba(255,255,255,0.2);
	text-align: center;
	font-size: px-to-rem(14px);
	line-height: px-to-rem(17px);
	color: #fff;
	padding: 12px 0;
}

.calc__btn {
	@include btn;
}

.calc__result {
	text-align: center;
	font-size: px-to-rem(17px);
	line-height: px-to-rem(24px);
	margin: 15px 0;
}



@media (min-width: 1065px) {
	.calc__title {
		padding-top: 65px;
	}
}

@media (min-width: 480px) {
	.calc__wr {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	.calc__title, .calc__input {
		flex-basis: 100%;
		width: 100%;
	}

	.calc__radio {
		margin-right: 19px;
		&:last-child {
			margin-right: 0;
		}
	}

	.calc__btn {
		flex-basis: 162px;
		width: 164px;
		margin-right: 25px;
	}

	.calc__result {
		text-align: left;
		font-size: px-to-rem(14px);
		line-height: px-to-rem(14px);
	}
}

@media (min-width: 1065px) {
	.calc {
		.fp-tableCell {
			vertical-align: top;
			padding-top: 65px;
		}
		&__title {
			padding-left: 460px;
			text-align: left;
			max-width: 100%;
			margin-top: 0;
			padding-top: 0;
		}
		&__img {
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(calc(-50% + 30px));
		}
		&__form {
			padding-left: 200px;
			margin-left: 262px;
			padding-top: 8px;
			padding-bottom: 20px;
			padding-right: 40px;
			background-color: #000;
			border-radius: 7px;
		}
		&__input {
			display: none;
		}
		&__range {
			display: flex;
			align-items: center;
			flex-basis: 100%;
			margin-top: 7px;
			margin-bottom: 7px;
			input[type="range"] {
				width: 280px;
				flex-basis: 280px;
				margin-top: 10px 0;
				appearance: none;
				outline: none;
				&::-webkit-slider-runnable-track {
					background-color: $yellows;
					width: 100%;
					height: 4px;
					cursor: pointer;
					border-radius: 8px;

				}
				&::-moz-range-track {
					background-color: $yellows;
					width: 100%;
					height: 4px;
					cursor: pointer;
					border-radius: 8px;
				}
				&::-ms-track {
					background-color: $yellows;
					width: 100%;
					height: 4px;
					cursor: pointer;
					border-radius: 8px;
				}
				&::-webkit-slider-thumb {
					width: 10px;
					height: 10px;
					border-radius: 50%;
					background-color: #fff;
					cursor: pointer;
					appearance: none;
					margin-top: -3px;
				}
				&::-moz-range-thumb {
					width: 10px;
					height: 10px;
					border-radius: 50%;
					background-color: #fff;
					cursor: pointer;
					appearance: none;
					margin-top: -3px;
				}
				&::-ms-thumb {
					width: 10px;
					height: 10px;
					border-radius: 50%;
					background-color: #fff;
					cursor: pointer;
					appearance: none;
					margin-top: -3px;
				}
			}
			output {
				margin-right: 20px;
				border: 1px solid #545454;
				padding: 7px 12px;
				border-radius: 3px;
				min-width: 60px;
				text-align: center;
			}
		}
	}
}