.features {
	&__item {
		padding: 24px;
		box-sizing: border-box;
		background: rgba(0,0,0,0.3);
		border-radius: 5px;
		text-align: center;
		margin-bottom: 24px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	&__text {
		font-size: px-to-rem(17px);
		line-height: px-to-rem(24px);
		margin-top: 14px;
		margin-bottom: 3px;
	}

}

@media (min-width: 1024px) {
	.features {
		display: flex;
		justify-content: center;
		max-width: 744px;
		margin: 0 auto;
		z-index: 1;
		&::after {
			content: '';
			position: absolute;
			background: rgba(0,0,0,0.5);
			height: 150px;
			width: 90%;
			left: 0;
			z-index: -1;
			border-radius: 0 72px 72px 0;

		}
	}

	.features__item {
		background: none;
		border-radius: none;
		flex-basis: 25%;
		padding-left: 10px;
		padding-right: 10px;
		margin-bottom: 0;
	}

	.features__text {
		font-size: px-to-rem(14px);
		line-height: px-to-rem(22px);
	}
}