.works {
	color: #fff;
	background-color: #181818;

	&__cont {
		padding-top: 5px;
		height: 100%;
		position: relative;
	}
	&__slider {
		padding-left: 2px;
		padding-right: 2px;
		box-sizing: border-box
	}
	&__title {
		color: $yellows;
		font-size: px-to-rem(32px);
		line-height: px-to-rem(37px);
		text-align: center;
		margin: 0;
		margin-bottom: 20px;
	}
	&__subtitle {
		font-size: px-to-rem(20px);
		line-height: px-to-rem(26px);
		text-align: center;
		margin-bottom: 35px;
	}
	&__big {
		margin-bottom: 4px;
		img {
			display: block;
			height: auto;
			max-width: 100%
		}
	}
	&__mini {
		display: flex;
		justify-content: space-between;
		margin-bottom: 35px;
		a {
			text-decoration: none;
			margin-right: 4px;
			&:last-child {
				margin-right: 0;
			}
			img {
				display: block;
				height: auto;
				max-width: 100%
			}
		}
	}
	&__bilo, &__task, &__result {
		margin-top: 25px;
		p {
			font-size: px-to-rem(17px);
			line-height: px-to-rem(24px);
			margin-bottom: 17px;
			margin-top: 0;
			&:first-child {
				font-size: px-to-rem(20px);
				line-height: px-to-rem(23px);
				margin-bottom: 10px;
			}
		}
	}
	&__task {
		ul {
			list-style: none;
			padding: 0;
			margin: 0;
			li {
				font-size: px-to-rem(17px);
				line-height: px-to-rem(24px);
				position: relative;
				padding-left: 16px;
				margin-bottom: 12px;

				&::after {
					content: '';
					width: 3px;
					height: 7px;
					background-color: $yellows;
					position: absolute;
					left: 2px;
					top: 8px;
					transform: rotate(45deg);
				}
			}
		}
	}
	&__details {
		border: 1px solid #545454;
		border-radius: 5px;
		margin: 0;
		margin-top: 30px;
		margin-bottom: 20px;
		box-sizing: border-box;
	}
	&__item {
		border-bottom: 1px solid #545454;
		padding: 13px 17px;
		box-sizing: border-box;
		&:last-child {
			border-bottom: none;		
		}
		p {
			margin: 0;
			font-size: px-to-rem(15px);
			line-height: px-to-rem(18px);
			color: #888;
			span {
				color: #fff;
			}
		}
	}
	&__count {
		position: absolute;
		left: 15px;
		bottom: 15px;
		background-color: rgba(0,0,0,0.77);
		padding: 10px;
		z-index: 1;
		border-radius: 5px;
		font-size: px-to-rem(14px);
		line-height: px-to-rem(22px);
		color: #888;
	}
	&__first {
		font-size: px-to-rem(20px);
		color: #fff;
	}
}

.works .fp-scroller {
	padding-bottom: 120px;
}


div.fp-controlArrow.fp-prev {
	border: none;
	background: url("../img/arrow-prev.svg");
	width: 24px;
	height: 24px;
	bottom: 25px;
	top: auto;
	left: calc(100% - 80px);
}

div.fp-controlArrow.fp-next {
	border: none;
	background: url("../img/arrow-next.svg");
	width: 24px;
	height: 24px;
	bottom: 25px;
	top: auto;
	left: calc(100% - 45px);
}


@media (min-width: 465px) {
	.works {
		&__gallery {
			justify-content: center;
			display: flex;
			a {
				margin-right: 0;
				margin-bottom: 8px;
				&::last-child {
					margin-bottom: 0;
				}
			}
		}
		&__big {
			margin-bottom: 0;
			img {
				height: auto;
			}
		}
		&__mini {
			flex-direction: column;
			margin-left: 7px;
		}
		&__bilo {
			margin-top: 0;
		}
	}
}

@media (min-width: 1065px) {

	.works__slider, .works .fp-tableCell, .works .fp-scroller  {
		display: grid;
		grid-template-columns: repeat(6, 1fr);
		grid-column-gap: 30px;
		align-content: center;
	}

	.fp-scrollable {
		grid-column: 1 / -1;
	}

	.works__title {
		text-align: left;
		grid-column: 1 / -1;
		margin-bottom: 15px;
		padding-top: 15px;
	}

	.works__subtitle {
		text-align: left;
		grid-column: 1 / -1;
		margin-bottom: 15px;
		margin-top: 10px;
	}

	.works__gallery {
		grid-column: 1 / 4;
		a {
			margin: 0;
		}
	}

	.works__info {
		grid-column: 4 / -1
	}

	.works__result {
		grid-row: 3 / 4
	}

	.works__details {
		grid-column: 1 / -1
	}

	.works__big {
		img {
			height: 100%;	
			object-fit: cover;
		}
	}

	.works__mini {
		justify-content: space-between;
		margin-bottom: 0;
	}

	.works__bilo, .works__result, .works__task {
		p {
			font-size: px-to-rem(14px);
			line-height: px-to-rem(22px);
			&:first-child {
				font-size: px-to-rem(18px);
				line-height: px-to-rem(21px);
			}
		}
	}

	.works__task {
		ul {
			li {
				font-size: px-to-rem(14px);
				line-height: px-to-rem(22px);
				margin-bottom: 8px;
				&::last-child {
					margin-bottom: 0;
				}
				&::after {
					top: 8px;
				}
			}
		}
	}

	.works__details {
		grid-column: 1 / -1;
		display: flex;
		flex-wrap: wrap;
		border: none;
		border-left: 1px solid #545454
	}

	.works__item {
		flex-basis: 33.3%;
		border: 1px solid #545454;
		margin-left: -1px;
		margin-top: -1px;
		&:last-child {
			border: 1px solid #545454;

		}
	}

}