	.about {
	background: url('../img/details-bg.jpg') center top no-repeat;
	background-size: cover;
	color: $white;
	position: relative;
	z-index: 1;
	padding-bottom: 100px;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.75);
		z-index: -1;
	}
	&__cont {
		padding-top: 120px;
		padding-bottom: 10px;
		position: relative;
	}
	.fp-tableCell {
		vertical-align: top;
	}
	&__title {
		@extend .works__title
	}
	&__subtitle {
		@extend .works__subtitle;
		font-size: px-to-rem(18px);
		line-height: px-to-rem(24px);
		margin-top: 20px;
		margin-bottom: 32px;
	}
	&__list {
		padding: 0;
		margin: 0;
		margin-bottom: 20px;
		list-style-type: none;
	}
	&__item {
		cursor: pointer;
		font-size: px-to-rem(17px);
		line-height: px-to-rem(24px);
		margin-bottom: 24px;
		color: $black;
		text-align: center;
		&::last-child {
			margin-bottom: 0;
		}
		span {
			background-color: #fff;
			display: block;
			border-radius: 5px;
			padding: 15px 10px;
		}
	}
}

@media (min-width: 1065px) {
	.about {
		&__title {
			max-width: 455px;
			padding-top: 60px;
			text-align: left;
		}
		&__subtitle {
			max-width: 554px;
			text-align: left;
			font-size: px-to-rem(24px);
			line-height: px-to-rem(32px);

		}
		&__list {
			position: relative;
			height: 276px;
		}
	&__item {
		position: absolute;
		&:nth-child(1) {
			left: 40px;
			top: 100px;
		}
		&:nth-child(2) {
			top: 200px;
			left: 360px;
		}
		&:nth-child(3) {
			top: 0;
			left: 550px;
		}
		&:nth-child(4) {
			right: 0;
			top: 120px;
		}
		span {
			padding-left: 40px;
			padding-right: 24px;
			padding-top: 32px;
			padding-bottom: 32px;
			width: 345px;
			font-size: px-to-rem(14px);
			line-height: px-to-rem(16px);
			text-align: left;
			box-sizing: border-box;
			visibility: hidden;
			opacity: 0;
			transition: opacity 550ms ease-in;

		}
		&:hover span {
			opacity: 1;
			visibility: visible;
		}
		&:hover::after {
			background-color: lighten($yellowsprim, 10);
		}
		&::after {
			content: '';
			background: url('../img/plus.svg') center no-repeat $yellows;
			width: 40px;
			height: 40px;
			display: block;
			border: 2px solid $black;
			border-left: none;
			position: absolute;
			left: -20px;
			top: 50%;
			transform: translateY(-50%);
			z-index: 1;
			border-radius: 50%;
			transition: 450ms background-color;

		}
	}
	}
}