.header {
	background: url('../img/screen-bg.jpg') 35% top no-repeat;
	position: relative;
	color: $white;
	z-index: 2;
	&::after {
		content: '';
		position: absolute;
		background-color: #111;
		opacity: 0.75;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		right: 0;
		z-index: -1;
	}
	&__title {
		font-size: px-to-rem(32px);
		line-height: px-to-rem(40px);
		color: $yellows;
		text-align: center;
		margin: 0;		
		margin-bottom: 24px;
		font-weight: 700;
	}
	&_cont {
		padding-top: 145px;
		&_2 {
			padding-bottom: 30px;
		}
	}
	&__subtitle {
		font-size: px-to-rem(24px);
		line-height: px-to-rem(28px);
		text-align: center;
		margin-bottom: 64px;
		font-weight: 400;

	}

	&__btn {
		@include btn {
			padding-top: 19px;
			padding-bottom: 19px;
			text-decoration: none;
			margin-left: auto;
			margin-right: auto;
		}
	}
	&_btndescr {
		background: url('../img/rectangle.png') center no-repeat;
		background-size: 288px 54px;
		font-size: px-to-rem(14px);
		line-height: px-to-rem(54px);
		display: flex;
		justify-content: space-between;
		max-width: 288px;
		margin: 35px auto;
		margin-bottom: 90px;
		position: relative;
		span:nth-child(1) {
			padding-left: 30px;
		}
		span:nth-child(2) {
			padding-right: 30px;
		}
		&::after {
			content: '';
			background: url("../img/medal.png") center no-repeat;
			width: 82px;
			height: 83px;
			position: absolute;
			left: 130px;
			top: 50%;
			transform: translateY(-50%);
		}


	}
	&__list {
		padding: 0;
		margin: 0;
		list-style-type: none;
	}
}


@media (min-resolution: 2dppx) {
	.header {
		background-image: url('../img/screen-bg-2x.jpg');
	}

	.header_btndescr::after {
		background-image: url('../img/medal-2x.png');
		background-size: cover;
	}

}

@media (min-width: 480px) {
	.header {
		background-position: center top;
		&__btn {
			flex-basis: 265px;
			max-width: 265px;
			margin: 0;
			margin-right: 24px;
			&s {
				display: flex;
				justify-content: center;
				align-items: center;
				margin-top: 30px;
				margin-bottom: 95px;
			}
		}
		&_btndescr {
			flex-basis: 288px;
			flex-shrink: 0;
			margin: 0;
		}
	}
}

@media (min-width: 665px) {
	.header__title {
		font-size: px-to-rem(44px);
		line-height: px-to-rem(53px);

	}

	.header__subtitle {
		font-size: px-to-rem(26px);
		line-height: px-to-rem(30px);
		margin-bottom: 30px;


	}
}

@media (min-width: 1065px) {
	.header {
		background-size: cover;
	}
	.header__title {
		font-size: px-to-rem(54px);
		line-height: px-to-rem(62px);
		padding-top: 60px;
		margin-bottom: 12px;

	}

	.header_cont {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: calc(100% - 237px);
		&_2 {
			padding-bottom: 72px;
		}
	}

	.header__subtitle {
		font-size: px-to-rem(32px);
		line-height: px-to-rem(36px);
		margin-top: 12px;
		margin-bottom: 30px;

	}
}