.contacts {
	
	background: url('../img/map.jpg');
	background-color: $black;
	padding-bottom: 10px;

	&__cont {
		padding-top: 100px;
	}

	&__title {
		@extend .about__title;
	}

	.fp-tableCell {
		vertical-align: top;
	}

	&__info {
		background-color: $white;
		border-radius: 7px;
		box-sizing: border-box;
		padding: 24px;
	}

	&__phone, &__mail, &__adrress {
		margin-bottom: 20px;
		span {
			display: block;
			font-weight: bold;
			font-size: px-to-rem(14px);
			line-height: px-to-rem(17px);
			padding-left: 30px;
			background: url('../img/tel.svg') left center no-repeat;
		}

		a {
			text-decoration: none;
			color: $black;
			font-size: px-to-rem(17px);
			line-height: px-to-rem(24px);
			margin-top: 0;
			display: inline-block;
			margin-left: 30px;
			transition: color 450ms;
			margin-top: 7px;
			&:hover {
				color: $yellows;
			}
		}
	}

	&__mail {
		span {
			background-image: url('../img/mail.svg');
		}
	}


	&__adrress {
		span {
			background-image: url('../img/map.svg');
			line-height: px-to-rem(24px);
			&:last-child {
				background: none;
				font-size: px-to-rem(17px);
				line-height: px-to-rem(24px);
				font-weight: 400;
				margin-top: 10px;
			}
		}
	}

	&__fast {
		font-size: px-to-rem(17px);
		line-height: px-to-rem(24px);
		margin-bottom: 0;
		padding-left: 30px;
		span {
			display: block;
		}
		a {
			color: $yellows;
			text-decoration: none;
		}
	}

}


@media (min-width: 1065px) {
	.contacts {
		&__title {
		}
		&__info {
			margin-top: 35px;
			max-width: 360px;
		}
		&__wrap {
			padding-bottom: 10px;
		}
	}
}