.fixed {
	position: fixed;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	z-index: 12;
	color: $white;
	box-sizing: border-box;
	background-color: rgba(0,0,0,0.8);
	width: 100%;
	&__cont {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
	}
	&__hamburger {
		flex-basis: 25px;
		flex-shrink: 0;
		cursor: pointer;
		margin-right: 11.3%;
		span {
			display: block;
			background-color: $yellows;
			height: 2px;
			border-radius: 5px;
			width: 100%;
			margin-bottom: 4px;
			&:nth-child(3) {
				margin-bottom: 0;
				width: 70%;
				margin-left: auto;
			}
		}
	}
	&__logo, &__map {
		background: url('../img/repair.svg') left center no-repeat;
		padding: 24px 0;
		padding-left: 48px;
		color: $white;
		text-decoration: none;
		p {
			margin: 0;
			font-size: px-to-rem(18px);
			line-height: px-to-rem(22px);
			span {
				display: block;
				font-size: px-to-rem(14px);
				line-height: px-to-rem(16px);
				color: #ccc;
			}
		}

	}
	&__phone {
		margin-left: auto;
		a {
			display: block;
			text-decoration: none;
			span {
				display: none;
				font-size: px-to-rem(24px);
				line-height: px-to-rem(28px);
				color: #fff;
			}
			img {
				display: block;
			}
		}
	}
	&__map {
		display: none;
		background-image: url('../img/map.png')
	}
	&__btn {
		@include btn {
			display: none;
			max-width: 164px;
			margin-left: 24px;
		}

	}
	&__cta {
		display: none;
		font-size: px-to-rem(14px);
		line-height: px-to-rem(16px);
		margin-left: 24px;
	}
}

@media (min-width: 480px) {
	.fixed__hamburger {
		margin-right: 10%;
	}

	.fixed__logo {
		margin-right: 6.5%;	
	}

	.fixed__map {
		display: block;
	}
}

@media (min-width: 665px) {
	.fixed__phone {
		a {
			img {
				display: none;
			}
			span {
				display: block;
			}
		}
	}
}

@media (min-width: 865px) {
	.fixed__btn {
		display: block;
	}
}

@media (min-width: 1065px) {
	.fixed__cta {
		display: block;
	}
	.fixed__hamburger {
		position: relative;
		&::after {
			content: 'Меню';
			font-size: px-to-rem(14px);
			line-height: px-to-rem(16px);
			position: absolute;
			right: -45px;
			top: 50%;
			transform: translateY(-50%);

		}
	}
}



